// src/stores/stateErrorMessage.ts
import { defineStore } from 'pinia';

interface StateErrorMessage {
    title?: string;
    message?: string;
    errorCode?: string;
}

export const useStateErrorMessageStore = defineStore('stateErrorMessage', {
    // state data
    state: (): StateErrorMessage => ({
        title: undefined,
        message: undefined,
        errorCode: undefined
    }),
    // actions methods
    actions: {
        //  this  state 
        setStateErrorMessage(data: StateErrorMessage) {
            this.title = data.title;
            this.message = data.message;
            this.errorCode = data.errorCode;
        },
        clearStateErrorMessage() {
            this.title = undefined;
            this.message = undefined;
            this.errorCode = undefined;
        }
    }
});
