import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileInfos } from '@/models/DTO/FileInfos';
import type { FileUploadResult } from '@/models/DTO/FileUploadResult';

/**
 * 
 */
export interface FileInfosManagementUpdateReq {
    business: string;
    caseNo: string;
    fileNo: string;
    isForceFileUpload?: null | true | false;
    fileName: string;
    docType: number;
    fileSrc: string | null;
    isActive: null | true | false;
    filePermission: string;
    description?: string | null;
    isUseRetention: null | true | false;
    deleteTime?: string | null;
}

/**
 * 
 */
export interface FileInfosManagementUpdateResp extends GeneralResp {
    result?: FileUploadResult;
}

/**
 * 
 */
const FileInfosManagementUpdate = (req: FileInfosManagementUpdateReq): Promise<FileInfosManagementUpdateResp> => {
    const url = RouteUrlList.API_FileInfosManagementUpdate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };

    //  API 
    const requestData = {
        fileNo: req.fileNo,
        business: req.business,
        caseNo: req.caseNo,
        fileName: req.fileName,
        docType: req.docType, // 
        isActive: req.isActive,
        filePermission: parseInt(req.filePermission), //  int 
        isForceFileUpload: req.isForceFileUpload,
        description: req.description,
        fileSrc: req.fileSrc, //  fileSrc  filePath
        isUseRetention: req.isUseRetention,
        deleteTime: req.deleteTime ? new Date(req.deleteTime).toISOString() : null
    };

    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<FileInfosManagementUpdateResp>) => {
        return res.data;
    });
};

export default FileInfosManagementUpdate;
