<template>
    <button
        v-if="showButton"
        @click="goToTop"
        class="go-top-button rounded-circle d-flex align-items-center justify-content-center p-2"
    >
        <i class="bi bi-arrow-up" style="font-size: 1.5rem"></i>
    </button>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, defineProps, watch } from 'vue';

//  props 
interface Props {
    // 
    scrollContainer?: HTMLElement | null;
}
//  props
const props = defineProps<Props>();

// 
const showButton = ref(false);

/**
 * 
 * 
 * @param func 
 * @param limit 
 * @returns 
 */
const throttle = (func: Function, limit: number) => {
    let inThrottle: boolean;
    return function (this: any, ...args: any[]) {
        if (!inThrottle) {
            func.apply(this, args);
            inThrottle = true;
            setTimeout(() => (inThrottle = false), limit);
        }
    };
};

/**
 * 
 * 
 */
const goToTop = () => {
    console.log('GoTop scrollContainer:', props.scrollContainer);
    if (props.scrollContainer && props.scrollContainer instanceof HTMLElement) {
        props.scrollContainer.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    } else {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }
};

/**
 * 
 *  300px 
 */
const handleScroll = () => {
    if (props.scrollContainer && props.scrollContainer instanceof HTMLElement) {
        console.log('handleScroll: scrollContainer.scrollTop =', props.scrollContainer.scrollTop);
        showButton.value = props.scrollContainer.scrollTop > 100;
    } else {
        console.log('handleScroll: window.scrollY =', window.scrollY);
        showButton.value = window.scrollY > 100;
    }
};

// 
const throttledHandleScroll = throttle(handleScroll, 100);

// 
let currentScrollTarget: HTMLElement | Window = window;

/**
 * 
 * @param target 
 */
const addScrollListener = (target: HTMLElement | Window) => {
    target.addEventListener('scroll', throttledHandleScroll);
};

/**
 * 
 * @param target 
 */
const removeScrollListener = (target: HTMLElement | Window) => {
    target.removeEventListener('scroll', throttledHandleScroll);
};

// 
onMounted(() => {
    console.log('onMounted: props.scrollContainer =', props.scrollContainer);
    if (props.scrollContainer && props.scrollContainer instanceof HTMLElement) {
        currentScrollTarget = props.scrollContainer;
    } else {
        currentScrollTarget = window;
    }
    addScrollListener(currentScrollTarget);
});

// 
onUnmounted(() => {
    removeScrollListener(currentScrollTarget);
});

//  scrollContainer prop 
watch(
    () => props.scrollContainer,
    (newVal, oldVal) => {
        console.log('watch: scrollContainer ', oldVal, '', newVal);
        // 
        if (oldVal && oldVal instanceof HTMLElement) {
            removeScrollListener(oldVal);
        } else if (!oldVal) {
            removeScrollListener(window);
        }
        // 
        if (newVal && newVal instanceof HTMLElement) {
            addScrollListener(newVal);
            currentScrollTarget = newVal;
        } else {
            addScrollListener(window);
            currentScrollTarget = window;
        }
    }
);
</script>

<style scoped>
.go-top-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: var(--esun-primary);
    color: white;
    border: none;
    border-radius: 50%;
    width: 72px;
    height: 72px;
    padding: 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10; /*  */
}

.go-top-button:hover {
    background-color: var(--esun-primary-hover);
}
</style>
